<template lang="html">
  <div class="timer-component">

    <div class="timer-clock">
      <div class="block">
        <p class="digit">{{ displayMinutes }}: </p>
      </div>
      <div class="block">
        <p class="digit">{{ computedSeconds }}</p>
      </div>
    </div>
  <transition name="fade" mode="out-in">
    <button v-if="start"v-on:click="Countdown(CountDownTime(minutes, seconds))">Start</button>
    <button v-else v-on:click="StopCountDown()">Stop</button>
  </transition>

  </div>
</template>

<script>
export default {
  name: "Timer",
  props: {
    minutes: {
      type: String
    },
    seconds: {
      type: String
    },
    _currentTea: {}
  },
  data() {
    return {
      displayMinutes: "0",
      displaySeconds: "0",
      start: true,
      timerInterval: {}
    };
  },
  watch: {
    _currentTea: function() {
      let self = this;
      self.displayMinutes = self.minutes;
      self.displaySeconds = "00";
      this.StopCountDown();
    }
  },
  mounted() {
    let self = this;
    self.displayMinutes = self.minutes;
    self.displaySeconds = "00";
  },
  methods: {
    Countdown: function(duration) {
      let self = this;
      self.start = false;
      self.timerInterval = setInterval(function() {
        duration = duration - 1;
        if (duration == -1) {
          self.CountDownFinished("./static/audio/gong.mp3");
          self.StopCountDown();
          return;
        }

        var seconds = duration % 60;
        var minutes = Math.floor(duration / 60);
        minutes %= 60;

        self.displayMinutes = minutes;
        self.displaySeconds = seconds;
      }, 1000);
    },
    StopCountDown: function() {
      let self = this;
      clearInterval(self.timerInterval);
      self.start = true;
      this.ResetCountDown();
    },
    ResetCountDown: function() {
      this.displayMinutes = this.minutes;
      this.displaySeconds = "00";
    },
    CountDownTime: function(minutes, seconds) {
      if (seconds === "0") {
        return 60 * minutes;
      } else return minutes + seconds;
    },
    CountDownFinished: function(sound) {
      if (sound) {
        var audio = new Audio(sound);
        audio.play();
      }
    }
  },
  computed: {
    computedSeconds: function() {
      if (
        parseInt(this.displaySeconds) >= 10 ||
        parseInt(this.displaySeconds) == 0
      ) {
        return this.displaySeconds;
      } else return "0" + this.displaySeconds;
    }
  }
};
</script>

<style lang="css">

.timer-component {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 70%;
}

.timer-clock {
  width: 50%;
  padding: 0.2em;
  display: flex;
  justify-content: center;
  align-items: center;
  border: solid 2px black;
  font-family: 'Merriweather Sans', sans-serif;
  font-size: 3rem;
  font-weight: bold;
}

button {
  width: 30%;
  height: 50px;
  border: solid 1px black;
  background-color: #fbf4e6;
  font-family: 'Merriweather Sans', sans-serif;
  font-size: 2rem;
}

@media only screen and (min-height: 900px) {
  .timer-component {
    height: 40%;
  }
  .timer-clock {
    width: 70% !important;
  }
}

@media only screen and (min-width: 650px) {

  .timer-component {
    justify-content: center;
    align-items: center;
  }

  .timer-clock {
    width: 40%;
    height: 150px;
    font-size: 5rem;
    margin-bottom: 20px;
  }
}

.fade-enter-active, .fade-leave-active {
  transition: opacity 5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>



