<template lang="html">
  <div class="container">

    <div class="header-selector">
      <div class="header-english">
        <h1>T E A<br>T I M E R</h1>
      </div>
      <div class="header-japanese">
        お茶
      </div>

      <div class="selector">
         <select v-model="selected">
            <option v-for="option in options" v-bind:value="option">
              {{ option.text }}
            </option>
         </select>
      </div>
    </div>

    <div class="timer">
      <div class="temperature">
        <span>
          {{ selected.temperature }}
        </span>
      </div>

      <!-- Insert minutes and seconds from current tea prop -->
      <Timer v-bind:minutes="selected.minutes" v-bind:seconds="selected.seconds" v-bind:_currentTea="selected"></Timer>
    </div>
  </div>
</template>

<script>
import Timer from "@/components/Timer";

export default {
  name: "Index",
  data() {
    return {
      selected: {
        text: "Black Tea",
        value: "A",
        temperature: "99℃",
        minutes: "2",
        seconds: "0"
      },
      options: [
        {
          text: "Black Tea",
          value: "A",
          temperature: "99℃",
          minutes: "2",
          seconds: "0"
        },
        {
          text: "White Tea",
          value: "B",
          temperature: "70℃",
          minutes: "4",
          seconds: "0"
        },
        {
          text: "Green Tea",
          value: "C",
          temperature: "80℃",
          minutes: "2",
          seconds: "0"
        },
        {
          text: "Chamomile Tea",
          value: "E",
          temperature: "100℃",
          minutes: "4",
          seconds: "0"
        },
        {
          text: "Jasmine Tea",
          value: "F",
          temperature: "71℃",
          minutes: "2",
          seconds: "0"
        },
        {
          text: "Rooibos",
          value: "G",
          temperature: "96℃",
          minutes: "3",
          seconds: "0"
        }
      ]
    };
  },
  methods: {
    currentTeaa: function(items) {
      //_TODO: Convert to method that returns object of tea
      let self = this;
    }
  },
  components: {
    Timer
  }
};
</script>

<style lang="css">
@import url('https://fonts.googleapis.com/css?family=Cormorant:700');
@import url('https://fonts.googleapis.com/css?family=Merriweather+Sans:700');

.container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.header-selector {
  width: 100%;
  height: 65%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content:flex-start;
}

.timer {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 35%;
  background-color: #fbf4e6;
}

.header-english {
  margin-top: 45px;
  position: relative;
  left: 5px;
  font-family: 'Cormorant', serif;
  font-size: 1.25rem;
  letter-spacing: 2px;
  text-align: center;
}

.header-japanese {
  width: 100%;
  font-family: 'Hiragino Mincho Pro', 'MS PMincho', serif;
  font-weight: 900;
  font-size: 2.5rem;
  text-align: center;
  margin-top: 5px;
}

.selector {
  width: 100%;
  height: 30%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding: 0.5em;
  font-size: 1.8rem;
}

select {
  width: 200px;
  height: 40%;
  border-left: solid 4px black;
  border-top: 0px;
  border-right: 0px;
  border-bottom: 0px;
  border-radius: 0px !important;
  font-family: 'Cormorant', serif;
  font-size: 1.5rem;
  text-align-last:center;
  background-color: #fbf4e6;
  -webkit-appearance:none;
}

.temperature {
  font-family: 'Merriweather Sans', sans-serif;
  font-size: 2rem;
  font-weight: bold;
}



select:active, select:hover {
  outline: none
}

@media only screen and (min-width: 650px) {

  .temperature {
    font-size: 6rem;
  }

  .container {
    flex-direction: row;
  }

  .header-selector {
    height: 100%;
    width: 40%;
  }

  .timer {
    height: 100%;
    width: 60%;
    align-items: center !important;
    justify-content: center !important;
  }

}

@media only screen and (min-width: 825px) {

  .header-english {
    left: 0;
    width: 80%;
  }

  .header-japanese {
    width: 80%;
  }

  select {
    width: 300px;
    height: 50%;
    font-size: 3rem;
  }
}

</style>



